import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPhoneAlt } from '@fortawesome/free-solid-svg-icons'
import LineIcon from '../assets/img/line_icon.svg'

export function Button(props) {
    return (
        <div className={`button ${ props.className }`} style={props.style}>
            {props.slot}
        </div>
	);
}

export function TextButton(props) {
	return (
        <div className={`text__button ${ props.className }`} style={props.style}>
            {props.slot}
        </div>
	);
}

export function PhoneButton(props) {
    var className = 'phone__button__sm'

    if (props.size === 'lg') {
        className = 'phone__button__lg'
    } else if (props.size === 'sm') {
        className = 'phone__button__sm'
    }

    return (
        <Button 
            className={`${className} ${ props.className }`}
            style={props.style}
            slot={
                <div className='mix__content'>
                    <FontAwesomeIcon icon={faPhoneAlt} className='phone__icon'/>
                    <span>020-791-801</span>
                </div>
            }
        />
	);
}

export function LineButton(props) {
    var className = 'line__button__sm'

    if (props.size === 'lg') {
        className = 'line__button__lg'
    } else if (props.size === 'sm') {
        className = 'line__button__sm'
    }

    return (
        <Button 
            className={`${className} ${ props.className }`}
            style={props.style}
            slot={
                <div className='mix__content'>
                    <img src={LineIcon} alt='Line' className='line__icon'/>
                    <span>{props.slot}</span>
                </div>
            }
        />
	);
}