import React from 'react';
import { Header, WhyChooseUs, ContactUs, AboutUs } from '../components/Content';
import GetStarted from '../components/GetStarted';
import ExpansionPanel from '../components/ExpansionPanel';
import GetStartedImage from '../assets/img/finance/get_started_01.svg';
import HeaderImage from '../assets/img/finance/menu_03.svg';
import MediaQuery from 'react-responsive';

function Refinance() {
	const summaryText01 = <span>รีไฟแนนซ์รถคือ?</span>

	const summaryText02 = <span>รีไฟแนนซ์รถได้หรือยัง?</span>

	const detailText01 = 
		<div style={{display: 'flex', flexDirection: 'column'}}>
			<span className='dark__blue'>การย้ายที่ผ่อนรถ โดยปิดบัญชีที่ยังผ่อนอยู่</span>
			<span className='dark__blue'>ด้วยเงินก้อนใหม่ที่กู้จากเรา ด้วยอัตราดอกเบี้ยใหม่</span>
			<span className='dark__blue'>วงเงินใหม่ (ประเมินจากตัวรถปัจจุบัน)</span>
		</div>

	const detailText02 = 
		<div style={{display: 'flex', flexDirection: 'column'}} className='expansion__detail__text__center'>
			<span className='dark__blue'>ผ่อนชำระค่างวดมาแล้ว <font style={{ fontWeight: 'bold', textDecoration: 'underline'}}>เกินครึ่ง</font> ของยอดทั้งหมด</span>
			<span className='dark__blue'>หรือ</span>
			<span className='dark__blue'>ยอดปิดบัญชีผ่อนที่เก่า <font style={{ fontWeight: 'bold', textDecoration: 'underline'}}>ควรต่ำกว่า</font> มูลค่ารถปัจจุบัน</span>
		</div>

	return (
		<div className='container'>
			<Header
				slot={
					<div>
						<div className='header__text'>
							<div>
								<span className='dark__blue'>รีไฟแนนซ์</span>
								<span className='blue'>รถ</span>
							</div>
						</div>

						<img src={HeaderImage} alt='Header' style={{width: 300}}/>
					</div>
				}
			/>

			<MediaQuery query="(max-device-width: 768px)">
				<ExpansionPanel
					summary={summaryText01}
					detail={detailText01}
				/>
				<ExpansionPanel
					summary={summaryText02}
					detail={detailText02}
				/>
			</MediaQuery>
			<MediaQuery query="(min-device-width: 769px)">
				<div className='duo__text__container'>
					<div className='left__text__container'>
						<div className='large__text dark__blue' style={{marginTop: 0}}>
							{summaryText01}
						</div>
						<div className='medium__text dark__blue detail__text'>
							{detailText01}
						</div>
					</div>
					<div className='right__text__container'>
						<div className='large__text dark__blue' style={{marginTop: 0}}>
							{summaryText02}
						</div>
						<div className='medium__text dark__blue detail__text'>
							{detailText02}
						</div>
					</div>
				</div>
			</MediaQuery>

			<GetStarted
				getStartedData={
					[{
						image: GetStartedImage,
						headerText:
							<div>
								<span className='dark__blue'>1. เช็คก่อนว่าสามารถ</span>
								<span className='blue'>รีไฟแนนซ์</span>
								<span className='dark__blue'>ได้หรือยัง</span>
							</div>,
						bodyText: 
							<span className='dark__blue'>
								ไม่ใช่รถทุกคันที่ผ่อนอยู่จะสามารถรีไฟแนนซ์ได้<br></br>
								<font style={{fontWeight: 'bold'}}>รถควรผ่อนมาแล้วเกินครึ่งของยอดทั้งหมด</font><br></br>
								จึงจะสามารถรีไฟแนนซ์รถได้
							</span>
					},{
						image: GetStartedImage,
						headerText:
							<div>
								<span className='dark__blue'>2. ติดต่อบริษัท</span>
								<span className='blue'>ไฟแนนซ์รถ</span>
								<span className='dark__blue'>เพื่อสรุปวงเงิน</span>
							</div>,
						bodyText: 
							<span className='dark__blue'>
								ติดต่อหาบริษัทไฟแนนซ์รถ เพื่อประเมินวงเงิน และ<br></br>
								ดอกเบี้ยในการจัดไฟแนนซ์รถ <br></br>
								ปัจจัยของวงเงินและดอกเบี้ยจะขึ้นกับ <font style={{ fontWeight: 'bold'}}>รถที่นำมารีไฟแนนซ์</font> <br></br>
								วงเงินที่ต้องการ และ <font style={{ fontWeight: 'bold'}}>รายได้ของผู้กู้</font>
							</span>
					},{
						image: GetStartedImage,
						headerText:
							<div>
								<span className='dark__blue'>3. จัดเตรียม</span>
								<span className='blue'>เอกสาร</span>
							</div>,
						bodyText: 
							<span className='dark__blue'>
								จัดเตรียมเอกสารต่างๆให้พร้อมเตรียมยื่นให้บริษัทไฟแนนซ์รถ<br></br>
								เช่น บัตรประชาชน, ทะเบียนบ้าน,<br></br>
								เอกสารแสดงรายได้ย้อนหลัง เป็นต้น<br></br>
								ยิ่งพร้อมเร็ว ยิ่งผ่านเร็วครับ
							</span>
					},{
						image: GetStartedImage,
						headerText:
							<div>
								<span className='dark__blue'>4. นัดเซ็น</span>
								<span className='blue'>สัญญา</span>
								<span className='dark__blue'> และ โอน</span>
								<span className='blue'>ทะเบียน</span>
							</div>,
						bodyText: 
							<span className='dark__blue'>
								หลังจากเอกสารพร้อม บริษัทไฟแนนซ์นัดเซ็นสัญญา<br></br>
								เมื่อเซ็นเรียบร้อย นัดโอนทะเบียนรถ<br></br>
								และรับรถไปขับได้ทันที (ใช้เวลา2-3วัน)
							</span>
					}]
				}
				 
			/>
			<WhyChooseUs/>
			<ContactUs/>
			<AboutUs/>
		</div>
	);
}

export default Refinance;
