import React from 'react';
import { Header, WhyChooseUs, ContactUs, AboutUs } from '../components/Content';
import Menu from '../components/Menu'

function Home() {
	return (
		<div className='container'>
			<Header
				slot={
					<div className='header__text'>
						<div>
							<span className='dark__blue'>จัดไฟแนนซ์</span>
							<span className='blue'>รถมือสอง</span>
						</div>
						<div>
							<span className='blue'>ครบวงจร</span>
						</div>
					</div>
				}
			/>
			<Menu/>
			<WhyChooseUs/>
			<ContactUs/>
			<AboutUs/>
		</div>
	);
}

export default Home;
