import React, { useState } from 'react';

import { Card } from '../components/Card';
import Swiper from 'react-id-swiper';
import MediaQuery from 'react-responsive';
import AlertIcon from '../assets/img/finance/alert_icon.svg';
import LeftArrow from '../assets/img/left_arrow.svg';
import RightArrow from '../assets/img/right_arrow.svg';

export default function GetStarted(props) {
    let GetStarted = []
    
    for (let i = 0; i < props.getStartedData.length; i++){
        GetStarted.push(
            <Card
                key={i}
                className='card__get-started'
                image={<img src={props.getStartedData[i].image} alt='Body_01' style={{height: '100%'}}/>}
                text={
                    <div>
                        <div className='large__text' style={{margin: 0}}>
                            {props.getStartedData[i].headerText}
                        </div>
                        <MediaQuery query="(max-device-width: 768px)">
                            <img src={AlertIcon} alt='AlertIcon' style={{width: 30, height: 30, marginTop: 8}}/>
                        </MediaQuery>
                        <MediaQuery query="(min-device-width: 769px)">
                            <div className='small__text' style={{marginTop: 20, marginLeft: 30, fontFamily: 'Sarabun'}}>
                                {props.getStartedData[i].bodyText}
                            </div>
                        </MediaQuery>
                    </div>
                }
                isInteraction={true}
                interaction={
                    <div className='small__text' style={{fontFamily: 'Sarabun'}}>
                        {props.getStartedData[i].bodyText}
                    </div>
                }
            />
        )
    }

    const [swiper, updateSwiper] = useState(null);
    const [page, setPage] = useState(0); 
    const [isBeginning, setIsBeginning] = useState(true);
    const [isEnd, setIsEnd] = useState(false);

    if (swiper !== null) {
        swiper.on('slideChange', function () {
            setPage(swiper.activeIndex)
            setIsBeginning(swiper.isBeginning)
            setIsEnd(swiper.isEnd)
        });
    }

    const goNext = () => {
        if (swiper !== null) {
            swiper.slideNext();
        }
    };
    
    const goPrev = () => {
        if (swiper !== null) {
            swiper.slidePrev();
        }
    };

	const params = {
		pagination: {
			el: '.carousel__pagination',
			type: 'bullets',
			clickable: true,
			renderBullet: function (index, className) {
				return `<span class="${className}">${index + 1}<br> </span>`;
			},
			bulletClass: "carousel__pagination__button--text",
			bulletActiveClass: "carousel__pagination__button--text--active"
		},
    }

	return (
		<div className='content'>
			<div className='large__text'>
				<div>
					<span className='blue'>เริ่มต้น</span>
					<span className='dark__blue'>อย่างไร?</span>
				</div>
			</div>

            <div className='carousel__container'>
                <Swiper containerClass="carousel__reveser" {...params} getSwiper={updateSwiper}>
                    {GetStarted}
                </Swiper>

                <MediaQuery query="(min-device-width: 769px)">
                    <div 
                        className='carousel__navigation__prev' 
                        style={{display: !isBeginning ? 'flex' : 'none'}} 
                        onClick={goPrev}
                    >
                        <img src={LeftArrow} alt='LeftArrow' style={{marginRight: 5}}/>
                        {page}.
                    </div>

                    <div 
                        className='carousel__navigation__next' 
                        style={{display: !isEnd ? 'flex' : 'none'}} 
                        onClick={goNext}
                    >   
                        {page + 2}.
                        <img src={RightArrow} alt='RightArrow' style={{marginLeft: 5}}/>
                    </div>
                </MediaQuery>
            </div>
		</div>
	)
}