import React from 'react';
import MediaQuery from 'react-responsive';
import { TextButton, PhoneButton, LineButton } from '../components/Button';
import WhyChooseImage01 from '../assets/img/finance/why_choose_01.svg';
import WhyChooseImage02 from '../assets/img/finance/why_choose_02.svg';
import WhyChooseImage03 from '../assets/img/finance/why_choose_03.svg';
import AboutUsImage from '../assets/img/finance/about_us.svg';
import LogoImage from '../assets/img/logo.svg';
import MarkerImage from '../assets/img/webicon128.png';
import FacebookImage from '../assets/img/social/facebook.svg';
import LineImage from '../assets/img/social/line.svg';
import WarningImage from '../assets/img/warning_icon.svg';
import { Card } from '../components/Card';
import GoogleMapReact from 'google-map-react';
import Swiper from 'react-id-swiper';


export function Header(props) {
	return (
		<div className='content' style={{paddingBottom: 30}}>
			<span className='medium__text blue' style={{marginTop: 40, fontFamily: 'Mitr', fontWeight: 'normal'}}>บริการ</span>
			{props.slot}	
		</div>
	)
}

export function WhyChooseUs() {
	var Card01 = 
		<Card
			className='card__why-choose-us'
			image={<img src={WhyChooseImage01} alt='Body_01' style={{width: '100%'}}/>}
			text={
				<div className='medium__text'>
					<div>
						<span className='dark__blue'>ไม่ใช่นายหน้า</span>
					</div>
					<div>
						<span className='dark__blue'>ค่าธรรมเนียม 0 บาท</span>
					</div>
				</div>
			}
		/>
	
	var Card02 = 
		<Card
			className='card__why-choose-us'
			image={<img src={WhyChooseImage02} alt='Body_02' style={{marginTop: 'auto', width: '100%'}}/>}
			text={
				<div className='medium__text'>
					<span className='dark__blue'>ให้ข้อเสนอที่คุณคาดไม่ถึง</span>					
				</div>
			}
		/>	

	var Card03 = 
		<Card
			className='card__why-choose-us'
			image={<img src={WhyChooseImage03} alt='Body_03' style={{width: '100%'}}/>}
			text={
				<div className='medium__text'>
					<div>
						<span className='dark__blue'>พนักงานมีประสปการณ์</span>	
					</div>
					<div>
						<span className='dark__blue' style={{width: '100%'}}>ไม่ต้องต่อสาย ปิดดีลทันที</span>				
					</div>
				</div>
			}
		/>	

	const params = {
		pagination: {
			el: '.carousel__pagination',
			type: 'bullets',
			clickable: true,
			bulletClass: "carousel__pagination__button",
			bulletActiveClass: "carousel__pagination__button--active"
		},
	}

	return (
		<div className='content'>
			<MediaQuery query="(min-device-width: 415px)">
				<div className='large__text'>
					<span className='dark__blue'>ทำไมต้องเลือก</span>
					<span className='blue'> Car ATM</span>
				</div>
			</MediaQuery>

			<MediaQuery query="(max-device-width: 414px)">
				<div className='large__text'>
					<span className='dark__blue'>ทำไมต้องเลือก Car ATM</span>
				</div>
			</MediaQuery>

			<MediaQuery query="(min-device-width: 1024px)">
				<div className='content__inline' style={{marginBottom: 30, flexDirection: 'row'}}>
					{Card01}
					{Card02}
					{Card03}
				</div>
			</MediaQuery>
			
			<MediaQuery query="(max-device-width: 1023px)">
				<Swiper containerClass="carousel" {...params}>
					<div style={{width: '100vw', maxWidth: 1440}}>
						{Card01}
					</div>
					<div style={{width: '100vw', maxWidth: 1440}}>
						{Card02}
					</div>
					<div style={{width: '100vw', maxWidth: 1440}}>
						{Card03}
					</div>
				</Swiper>
			</MediaQuery>
		</div>
	)
}

export function ContactUs() {
	return (
		<div className='content' style={{backgroundColor: '#F2F2F2'}}>
			<div className='large__text'>
				<span className='dark__blue'>ติดต่อเลย ปิดดีลได้ทันที</span>
			</div>
			
			<PhoneButton size={'lg'}/>
			<LineButton size={'lg'} style={{marginTop: 16}} slot={'ติดต่อผ่านไลน์'}/>
			<TextButton 
				slot='ให้เราติดต่อกลับ'
				style={{marginTop: 30, marginBottom: 30}}/>
			
			<span className='warning__text'>
				<img src={WarningImage} alt='warning' style={{height: '40%', marginRight: 8}}/>
				<span>เฉพาะรถยนต์ หรือ รถกระบะ ใหม่กว่าปี 2004 (2547)</span>
			</span>
		</div>
	)
}

const Marker = () => 
	<div>
		<img src={MarkerImage} alt='Logo' className="marker"/>
	</div>;

export function AboutUs() {
	return (
		<div className='content'>
			<div className='large__text'>
				<div>
					<span className='blue'>เชื่อใจ</span>
					<span className='dark__blue'>เรา</span>
				</div>
				<div>
					<span className='dark__blue'>ให้บริการสินเชื่อ</span>
					<span className='blue'>กว่า 30 ปี</span>
				</div>
			</div>

			<img src={AboutUsImage} alt='About us' className='about__image'/>
			<img src={LogoImage} alt='Logo' className='about__logo__image'/>

			<div className='small__text'>
				<div>
					<span className='dark__blue'>บริษัท คลังเศรษฐการ จำกัด</span>
				</div>
				<div>
					<span className='dark__blue'>238/16 รัชดาภิเษก ห้วยขวาง กรุงเทพมหานคร 10310</span>
				</div>
				<div>
					<span className='dark__blue'>ติดต่อเรา</span>
					<span className='blue'> 02-079-1801</span>
				</div>
			</div>

			<div style={{margin: 20}}>
				<img src={FacebookImage} alt='Facebook' className='social__image'/>
				<img src={LineImage} alt='Line' className='social__image' style={{marginLeft: 20}}/>
			</div>

			<div style={{width: '100%', height: 310 }}>
				<GoogleMapReact
					bootstrapURLKeys={{ key: 'AIzaSyAEt_UVZ8Dzc8daNRmyR8D2_2Wdw4V19Ng' }}
					defaultCenter={{
						lat: 13.7845583,
						lng: 100.5750718
					}}
					defaultZoom={18}
				>
					<Marker
						lat={13.7845583}
						lng={100.5750718}
					/>
				</GoogleMapReact>
			</div>
		</div>
	)
}