import React from 'react';
import MediaQuery from 'react-responsive';
import HamburgerMenu from 'react-hamburger-menu';
import Logo from '../assets/img/logo.svg';
import { TextButton, PhoneButton, LineButton } from './Button';
import { Link } from "react-router-dom";
import LineIcon from '../assets/img/line_icon.svg'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPhoneAlt } from '@fortawesome/free-solid-svg-icons'

export default class Navbar extends React.Component{
	constructor(props) {
		super(props);
		this.state = {
			open: false
		};
	}

	resetMenu(){
		this.setState({
			open: false
		});
	}

	openMenu() {
		this.setState({
			open: !this.state.open
		});
	}

	render() {
		return (
			<div>
				<div className='navbar'>
					<div className='navbar__container'>
						<img src={Logo} alt='Logo' className='navbar__logo'/>
						<MediaQuery query="(min-device-width: 1025px)">
							<div className='navbar__buttons'>
								<TextButton 
									slot={
										<Link to='/'>ทำไมต้องเลือกเรา?</Link>
									}
								/>

								<TextButton 
									slot={
										<Link to='/about'>เกี่ยวกับเรา</Link>
									}
								/>

								<TextButton 
									slot={
										<Link to='/about'>บทความ</Link>
									}
								/>	

								<TextButton 
									slot={
										<Link to='/'>บริการอื่นๆ</Link>
									}
								/>
							</div>
							<div className='navbar__contacts'>
								<LineButton style={{marginRight: 15}}/>
								<PhoneButton/>
							</div>
						</MediaQuery>
						<MediaQuery query="(max-device-width: 1024px)">
							<div className='navbar__contacts'>
								<HamburgerMenu
									isOpen={this.state.open}
									menuClicked={this.openMenu.bind(this)}
									width={30}
									height={20}
									strokeWidth={4}
									rotate={0}
									color='#244084'
									borderRadius={5}
									animationDuration={0.5}
									style={{margin: 'auto'}}
								/>
							</div>
						</MediaQuery>
					</div>
				</div>
				
				<MediaQuery query="(max-device-width: 1025px)">
					<div className={"m-navbar__menu " + (this.state.open ? 'active' : '')}>
						<Link className='m-navbar__button' to='/' onClick={this.resetMenu.bind(this)}>
							ทำไมต้องเลือก Car ATM ?
						</Link>
						
						<Link className='m-navbar__button' to='/about' onClick={this.resetMenu.bind(this)}>
							เกี่ยวกับเรา
						</Link>
						<Link className='m-navbar__button' to='/' onClick={this.resetMenu.bind(this)}>
							บทความเกี่ยวกับไฟแนนซ์รถ
						</Link>
						<Link className='m-navbar__button' to='/' onClick={this.resetMenu.bind(this)}>
							บทความ
						</Link>
						<Link className='m-navbar__button' to='/' onClick={this.resetMenu.bind(this)}>
							บริการอื่นๆ
						</Link>

						<Link className='m-navbar__contact' style={{marginTop: 'auto', fontFamily: 'Sarabun', fontWeight: 'bold'}} to='/'>
							020-791-801
							<div className='m-navbar__icon' style={{backgroundColor: '#fff'}}>
								<FontAwesomeIcon icon={faPhoneAlt}/>
							</div>
						</Link>
						<Link className='m-navbar__contact' to='/'>
							ติดต่อผ่านไลน์
							<div className='m-navbar__icon' style={{backgroundColor: '#00B900'}}>
								<img src={LineIcon}  alt='Line' />
							</div>
						</Link>
					</div>
				</MediaQuery>
			</div>
		);
	}
}
